.reminder-wrap {
	position: absolute;
	bottom: 2rem;
	left: 0;
	width: 100%;
	.reminder-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: 2rem;
		width: 100%;
		padding: 0 3rem;
		@include max-lg {
			gap: 1rem;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
