.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 500;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 28px;
			font-family: $font-secondary;
		}
	}
	.bg-couple {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 225%;
		@include max-lg {
			height: 150%;
		}
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
		&-body {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				opacity: 0.5;
				border-radius: var(--border-radius);
				box-shadow: var(--shadow);
				content: "";
			}
		}
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 90%;
			position: relative;
			aspect-ratio: 5/6;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 300px 300px 8px 8px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem;
		}
		.couple-parent {
			position: relative;
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			margin: 0 auto;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
