.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.rsvp-wrapper {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-dark);
		border-radius: var(--border-radius);
		margin-top: 15em;
		padding: 3rem 2rem;
		@include max-lg {
			margin-top: -8em;
		}
		@include max-md {
			padding: 2rem 1rem;
			margin-top: -5rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 120%;
		height: auto;
		aspect-ratio: 5/6;
		transform: translateX(-20%);
		@include max-lg {
			width: 100%;
			transform: none;
		}
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 300px 300px 8px 8px;
		}
	}
}
