.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		box-shadow: var(--shadow);
		margin-top: 15em;
		padding: 3rem 2rem;
		@include max-lg {
			margin-top: -8rem;
		}
		@include max-md {
			padding: 2rem 1rem;
			margin-top: -5rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 120%;
		height: auto;
		aspect-ratio: 3/4;
		@include max-lg {
			width: 100%;
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: 300px 300px 8px 8px;
			}
		}
	}
}
