.thank-section {
	position: relative;
	padding: 5rem 0 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/8;
		margin: 0 auto;
		@include max-lg {
			aspect-ratio: 8/7;
		}
		@include max-md {
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 1rem;
			&-body {
				position: relative;
				display: flex;
				justify-content: space-between;
				@include min-md {
					.title-section {
						text-align: left;
						&-ornament {
							margin: 0 0 0.25rem;
						}
					}
				}
				@include max-md {
					flex-direction: column;
				}
				.thank-description {
					position: relative;
					text-align: center;
					@include min-md {
						text-align: left;
						padding-top: 80px;
					}
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
