.venue-section {
	position: relative;
	.venue-wrapper {
		position: relative;
		z-index: 3;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.venue-description {
			position: relative;
			margin: 0 auto 2rem;
			z-index: 2;
			text-align: center;
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				margin-bottom: 1rem;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-button-trans);
					}
				}
			}
			.card {
				background-color: transparent;
				text-align: center;
				border: none;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					right: 0;
					background-color: var(--color-primary);
					opacity: 0.5;
					border-radius: var(--border-radius);
					content: "";
				}
				.card-body {
					position: relative;
					padding: 3rem 2rem;
					@include max-md {
						padding: 2rem 1rem;
					}
					h4 {
						padding: 0 0 1rem;
					}
					.event-name {
						position: relative;
						width: 100%;
						margin-bottom: 1rem;
						.date {
							position: relative;
							width: 100%;
							padding: 1rem 0;
							h5 {
								font-weight: 700;
								margin-bottom: 0;
								font-size: 20px;
							}
						}
						h4 {
							font-family: $font-secondary;
						}
						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}
					.event-description {
						position: relative;
						margin-bottom: 1rem;
					}
					.event-place {
						position: relative;
						.btn-custom {
							background-color: transparent;
							color: var(--color-button-trans);
							border: 1px solid var(--color-button-trans);
							&:hover {
								background-color: var(--color-secondary);
								color: var(--text-button);
								border: 1px solid var(--color-secondary);
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		position: relative;
		padding: 2rem;
		text-align: center;
		margin-bottom: 2rem;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			opacity: 0.5;
			border-radius: var(--border-radius);
			content: "";
		}
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
