.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: absolute;
		bottom: 30%;
		left: 50%;
		transform: translate(-50%, 0);
		@include max-lg {
			bottom: 0;
			transform: translate(-50%, 50%);
		}
	}
	.header-content {
		position: relative;
		.header {
			position: relative;
			width: 100%;
			height: 100%;
			margin: 0 auto;
			padding: 5rem 0 0;
			z-index: 5;
			.header-title {
				position: relative;
				&-content {
					position: relative;
					display: flex;
					justify-content: space-between;
					align-items: flex-end;
					.title {
						position: relative;
						width: 50%;
						text-align: left;
					}
					.header-description {
						position: relative;
						width: 50%;
						text-align: right;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 100%;
				height: auto;
				aspect-ratio: 16/8;
				box-shadow: var(--shadow);
				@include max-lg {
					aspect-ratio: 7/8;
				}
				@include max-md {
					aspect-ratio: 5/6;
				}
				.image-element {
					width: 100%;
					height: 100%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
